<template>
  <a-table :columns="columns" :data-source="tableData" :pagination="pagination">
    <div slot="name" slot-scope="text, record">
      <a-button @click="handleEdit(record)">编辑</a-button>
      <a-button @click="handleRemove(record)">删除</a-button>
    </div>
  </a-table>
</template>
<script>
const columns = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
  },
  {
    title: '标题',
    dataIndex: 'title',
    key: 'title',
    ellipsis: true,
  },
  {
    title: '描述',
    dataIndex: 'desc',
    key: 'desc',
    ellipsis: true,
  },
  {
    title: '类目',
    dataIndex: 'category',
    key: 'category',
  },
  {
    title: '预售价格',
    dataIndex: 'price',
    key: 'price',
  },
  {
    title: '折扣价格',
    dataIndex: 'price_off',
    key: 'price_off',
  },
  {
    title: '标签',
    dataIndex: 'tags',
    key: 'tags',
  },
  {
    title: '限制购买数量',
    dataIndex: 'inventory',
    key: 'inventory',
  },
  {
    title: '上架状态',
    dataIndex: 'status',
    key: 'status',
    customRender(text) {
      return text === 1 ? '上架' : '下架';
    },
  },
  {
    title: '操作',
    dataIndex: 'name',
    key: 'name',
    width: 200,
    scopedSlots: { customRender: 'name' },
  },

];

export default {
  props: ['data', 'pagination'],
  data() {
    return {
      columns,
    };
  },
  computed: {
    tableData() { // 一定要有一个唯一的key值
      return this.data.map((item) => ({
        ...item,
        key: item.id,
      }));
    },
  },
  methods: {
    handleEdit(record) {
      this.$emit('edit', record);
    },
    handleRemove(record) {
      this.$emit('remove', record);
    },
  },
};
</script>
